@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "Open Sans";
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
html {
  height: 100%;
}
body {
  display: flex;
  flex-direction: column;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  font-size: 15px;
  line-height: 24px;
  font-family: "Open Sans", "Arial", sans-serif;
}
a {
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}
.scroll-lock {
  overflow: hidden;
}
.about {
  max-width: 1150px;
  margin: 0 auto;
  padding: 31px 30px 29px 30px;
}
@media (min-width: 768px) {
  .about {
    padding: 55px 50px 41px 50px;
  }
}
@media (min-width: 1150px) {
  .about {
    padding: 68px 50px 45px 225px;
  }
}
.about__title {
  margin: 0;
}
.about__list {
  display: grid;
  margin: 0;
  padding: 0;
  list-style: none;
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  column-gap: 70px;
  grid-template-columns: repeat(auto-fill, 265px);
}
@media (min-width: 768px) {
  .about__list {
    font-size: 20px;
    line-height: 22px;
    column-gap: 35px;
    grid-template-columns: repeat(auto-fill, 300px);
    row-gap: 11px;
  }
}
@media (min-width: 1150px) {
  .about__list {
    column-gap: 50px;
    row-gap: 12px;
  }
}
.about__item {
  position: relative;
  margin-top: 83px;
}
.about__item::before {
  content: "";
  position: absolute;
  background-repeat: no-repeat;
}
.about__item:nth-child(even) {
  order: 1;
}
.about__item--material::before {
  bottom: calc(100% + 16px);
  left: 8px;
  width: 33px;
  height: 38px;
  background-image: url("../img/svg/material-icon.svg");
}
.about__item--design::before {
  bottom: calc(100% + 26px);
  left: 0;
  width: 47px;
  height: 27px;
  background-image: url("../img/svg/design-icon.svg");
}
.about__item--likes::before {
  bottom: calc(100% + 21px);
  left: 7px;
  width: 34px;
  height: 27px;
  background-image: url("../img/svg/likes-icon.svg");
}
.about__item--handmade::before {
  bottom: calc(100% + 17px);
  left: 3px;
  width: 35px;
  height: 34px;
  background-image: url("../img/svg/handmade-icon.svg");
}
.about__item--native::before {
  bottom: calc(100% + 26px);
  left: -6px;
  width: 55px;
  height: 23px;
  background-image: url("../img/svg/native-icon.svg");
}
.about__item--package::before {
  bottom: calc(100% + 20px);
  left: 3px;
  width: 33px;
  height: 32px;
  background-image: url("../img/svg/package-icon.svg");
}
@media (min-width: 768px) {
  .about__item {
    margin-top: 99px;
  }
  .about__item:nth-child(even) {
    order: 0;
  }
}
@media (min-width: 1150px) {
  .about__item {
    margin-top: 97px;
  }
}
.button {
  display: block;
  width: 100%;
  padding: 14px;
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  font-family: "Open Sans", "Arial", sans-serif;
  text-align: center;
  color: #444444;
  text-transform: uppercase;
  background: rgba(0, 0, 0, 0);
  border: 2px solid #444444;
  cursor: pointer;
  transition: background-color 0.6s ease, color 0.2s ease;
}
.button:hover {
  color: #ffffff;
  background-color: #444444;
}
.button:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #444444;
}
.button:focus-visible {
  border-color: #63d1bb;
  outline: none;
}
@media (min-width: 768px) {
  .button {
    padding: 15px;
    font-size: 17px;
  }
}
.carousel {
  position: relative;
}
.carousel__quote {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 0;
}
@media (min-width: 768px) {
